// src/fcm.js
import { messaging, db } from "./firebase";
import { getToken, onMessage } from "firebase/messaging";
import { doc, setDoc } from "firebase/firestore";

const VAPID_KEY = "BMWpaiJ1mta1AHaUwuwtWYMJ6AniydPjorh-6_d3AwbNVs9rgGFG_rLJE8xW-CPdyBwSq6SEI2I7CX0Qqaa8Ghk";

export async function registerFcmToken(userId) {
  try {
    const token = await getToken(messaging, { vapidKey: VAPID_KEY });
    if (token) {
      await setDoc(doc(db, "users", userId), { fcm_token: token }, { merge: true });
      console.log("FCM token saved:", token);
    }
  } catch (err) {
    console.log("FCM registration error:", err);
  }
}

// Optional: Listen for foreground messages
export function listenForMessages() {
  onMessage(messaging, (payload) => {
    alert((payload.notification?.title || "") + "\n" + (payload.notification?.body || ""));
  });
}
