import React, { useState, useEffect, useCallback } from "react";
import "./App.css";
import axios from "axios";
import Loader from "./Loader";
import "./Loader.css";
import { registerFcmToken, listenForMessages } from "./fcm";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { auth } from "./firebase";
import AuthForm from "./AuthForm";
import AdminPage from "./AdminPage";
import { onAuthStateChanged } from "firebase/auth"; // <-- Add this import

const API_URL = process.env.REACT_APP_API_URL;

function App() {
  const [user, setUser] = useState(null);
  const [products, setProducts] = useState([]);
  const [url, setUrl] = useState("");
  const [threshold, setThreshold] = useState("");
  const [phone, setPhone] = useState("");
  const [loading, setLoading] = useState(false);
  const [showProfile, setShowProfile] = useState(false);
  const [showAdmin, setShowAdmin] = useState(false);

  // Persistent login: Listen for auth state changes
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (firebaseUser) => {
      setUser(firebaseUser);
    });
    return () => unsubscribe();
  }, []);

  // Define fetchProducts with useCallback above useEffect
  const fetchProducts = useCallback(async () => {
    if (!user) return;
    setLoading(true);
    try {
      const res = await axios.get(`${API_URL}/user_products/`, { params: { user_id: user.uid } });
      setProducts(res.data.products || []);
    } catch (err) {
      toast.error("Failed to fetch products");
    }
    setLoading(false);
  }, [user]);

  useEffect(() => {
    if (user) {
      fetchProducts();
      registerFcmToken(user.uid);
      listenForMessages();
    }
  }, [user, fetchProducts]);

  const handleTrack = async (e) => {
    e.preventDefault();
    setLoading(true);
    if (!url || !threshold || !phone) {
      toast.error("All fields are required!");
      setLoading(false);
      return;
    }
    try {
      await axios.post(`${API_URL}/register_product/`, new URLSearchParams({
        url,
        threshold_price: threshold,
        phone,
        user_id: user.uid
      }));
      toast.success("Product registered and tracking started!");
      setUrl(""); setThreshold(""); setPhone("");
      await fetchProducts();
    } catch (err) {
      toast.error("Failed to register product");
    }
    setLoading(false);
  };

  const handleLogout = () => {
    auth.signOut();
    setUser(null);
  };

  if (!user) return <AuthForm setUser={setUser} />;
  // Admin access: show admin page if user is admin
  if (showAdmin) return <AdminPage />;

  return (
    <div className="app-container">
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary px-3">
        <a className="navbar-brand d-flex align-items-center" href="/" tabIndex={-1} aria-disabled="true" onClick={e => e.preventDefault()}>
          <img src="/logo.png" alt="Logo" className="logo me-2" style={{height: 40}} />
          <span className="app-title">PricePing</span>
        </a>
        <div className="ms-auto d-flex align-items-center gap-2">
          <span className="text-white me-2">{user?.email}</span>
          <button className="btn btn-outline-light btn-sm" onClick={() => setShowProfile(true)}>Profile</button>
          {/* Show admin button for a specific admin email (customize as needed) */}
          {user?.email === "admin@example.com" && (
            <button className="btn btn-warning btn-sm" onClick={() => setShowAdmin(true)}>Admin</button>
          )}
          <button className="btn btn-light btn-sm" onClick={handleLogout}>Logout</button>
        </div>
      </nav>
      <ToastContainer position="top-right" autoClose={3000} />
      {loading && <Loader />}
      {/* Profile Modal */}
      {showProfile && (
        <div className="modal fade show d-block" tabIndex="-1" style={{background: 'rgba(0,0,0,0.3)'}} onClick={() => setShowProfile(false)}>
          <div className="modal-dialog" onClick={e => e.stopPropagation()}>
            <div className="modal-content p-4">
              <h2>User Profile</h2>
              <p>Email: {user?.email}</p>
              <h3>Tracked Products</h3>
              <ul className="list-group mb-3">
                {products.map(p => (
                  <li className="list-group-item d-flex flex-column flex-md-row align-items-md-center justify-content-between gap-2" key={p.id}>
                    <span className="flex-grow-1">{p.url} (₹{p.current_price})</span>
                    <span className="badge bg-secondary">{p.active ? 'Tracking' : 'Paused'}</span>
                    <span className="small text-muted">Last checked: {p.last_checked ? new Date(p.last_checked).toLocaleString() : 'Never'}</span>
                    <button
                      className={`btn btn-sm ${p.active ? 'btn-warning' : 'btn-success'}`}
                      onClick={async () => {
                        try {
                          await axios.post(`${API_URL}/toggle_product/`, new URLSearchParams({
                            user_id: user.uid,
                            product_id: p.id,
                            active: (!p.active).toString()
                          }));
                          toast.success(p.active ? 'Tracking paused' : 'Tracking resumed');
                          fetchProducts();
                        } catch {
                          toast.error('Failed to toggle tracking');
                        }
                      }}
                    >
                      {p.active ? 'Pause' : 'Resume'}
                    </button>
                  </li>
                ))}
              </ul>
              <button className="btn btn-primary" onClick={() => setShowProfile(false)}>Close</button>
            </div>
          </div>
        </div>
      )}
      <main className="container my-4">
        <form className="row g-3 align-items-end" onSubmit={handleTrack}>
          <div className="col-md-5">
            <input type="url" className="form-control" placeholder="Product URL" value={url} onChange={e => setUrl(e.target.value)} required />
          </div>
          <div className="col-md-3">
            <input type="number" className="form-control" placeholder="Threshold Price" value={threshold} onChange={e => setThreshold(e.target.value)} required />
          </div>
          <div className="col-md-3">
            <input type="tel" className="form-control" placeholder="Phone Number" value={phone} onChange={e => setPhone(e.target.value)} required />
          </div>
          <div className="col-md-1">
            <button type="submit" className="btn btn-primary w-100">Track</button>
          </div>
        </form>
        <div className="row mt-4 g-4 justify-content-center">
          {products.map(product => (
            <div className="col-12 col-sm-6 col-md-4 col-lg-3" key={product.id}>
              <div className="card h-100 shadow-sm">
                <img src={product.image_url || "/logo.png"} alt="Product" className="card-img-top product-img" style={{objectFit: 'contain', height: 140}} />
                <div className="card-body text-center">
                  <div className="product-url small text-primary mb-2">{product.url}</div>
                  <div className="product-price h5 mb-1">₹{product.current_price}</div>
                  <div className="product-stock small text-danger">{product.stock}</div>
                  <div className="mb-2">
                    <span className={`badge ${product.active ? 'bg-success' : 'bg-secondary'}`}>{product.active ? 'Tracking' : 'Paused'}</span>
                  </div>
                  <div className="small text-muted mb-2">Last checked: {product.last_checked ? new Date(product.last_checked).toLocaleString() : 'Never'}</div>
                  <button
                    className={`btn btn-sm ${product.active ? 'btn-warning' : 'btn-success'}`}
                    onClick={async () => {
                      try {
                        await axios.post(`${API_URL}/toggle_product/`, new URLSearchParams({
                          user_id: user.uid,
                          product_id: product.id,
                          active: (!product.active).toString()
                        }));
                        toast.success(product.active ? 'Tracking paused' : 'Tracking resumed');
                        fetchProducts();
                      } catch {
                        toast.error('Failed to toggle tracking');
                      }
                    }}
                  >
                    {product.active ? 'Pause' : 'Resume'}
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </main>
    </div>
  );
}

export default App;