import React, { useState } from "react";
import { auth } from "./firebase";
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL;

export default function AuthForm({ setUser }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLogin, setIsLogin] = useState(true);
  const [error, setError] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    try {
      if (isLogin) {
        const userCred = await signInWithEmailAndPassword(auth, email, password);
        setUser(userCred.user);
      } else {
        const userCred = await createUserWithEmailAndPassword(auth, email, password);
        setUser(userCred.user);
        // Register user in backend Firestore
        await axios.post(`${API_URL}/register_user/`, new URLSearchParams({
          user_id: userCred.user.uid,
          email: userCred.user.email
        }));
      }
    } catch (err) {
      setError(err.message);
    }
  };

  return (
    <div className="container d-flex align-items-center justify-content-center min-vh-100">
      <div className="card shadow p-4" style={{ maxWidth: 400, width: "100%" }}>
        <div className="text-center mb-3">
          <img src="/logo.png" alt="Logo" style={{ height: 48 }} className="mb-2" />
          <h2 className="fw-bold mb-0">{isLogin ? "Login" : "Register"}</h2>
        </div>
        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <input type="email" className="form-control" placeholder="Email" value={email} onChange={e => setEmail(e.target.value)} required />
          </div>
          <div className="mb-3">
            <input type="password" className="form-control" placeholder="Password" value={password} onChange={e => setPassword(e.target.value)} required />
          </div>
          <button type="submit" className="btn btn-primary w-100 mb-2">{isLogin ? "Login" : "Register"}</button>
        </form>
        <button onClick={() => setIsLogin(!isLogin)} className="btn btn-link w-100 mb-2">
          {isLogin ? "Need an account? Register" : "Already have an account? Login"}
        </button>
        {error && <div className="alert alert-danger mt-2">{error}</div>}
      </div>
    </div>
  );
}
